import { Component, Directive, Input } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { NotificationItem } from '@backbase/notifications-common-ang';
import { SeverityLevelIconMap, severityLevelIconMap } from '../../../models/severity-level-icon-map';
import { Observable } from 'rxjs';
import { WidgetPropertiesService } from '../../../services/widget-properties.service';

@Component({
  selector: 'bb-notification-details-item',
  templateUrl: './notification-details-item.component.html',
})
export class NotificationDetailsItemComponent {
  /**
   * Notifications severity map
   */
  readonly levelMap: SeverityLevelIconMap = severityLevelIconMap;

  /**
   * Notification item data.
   */
  @Input() notification!: NotificationItem;

  /**
   * Format to display date in past
   */
  @Input() dateFormat: string | undefined;

  /**
   * Stream of a flag that defines if we should display relative time for today.
   */
  readonly displayRelativeTime$: Observable<boolean> = this.properties.displayRelativeTime$;

  /**
   * Getter for icon name from severity level icon map due to level of notification.
   */
  get icon(): string {
    return this.levelMap[this.notification.level].icon;
  }

  /**
   * Getter for icon color from severity level icon map due to level of notification.
   */
  get color(): string {
    return this.levelMap[this.notification.level].color;
  }

  constructor(private readonly properties: WidgetPropertiesService) {}
}

@Directive({
  selector: '[bbNotificationDetailsItemCustomizable]',
})
export class NotificationDetailsItemCustomizableDirective extends BbTemplate<NotificationDetailsItemComponent, void> {}
